<template>
    <div>
        <div class="btn">
            <el-button type="primary" @click="addRole">添加角色</el-button>
        </div>

        <el-dialog title="添加角色" :visible.sync="dialogRole" align="center">
            <div class="box">
                <el-form :model="form">
                    <el-form-item label="角色名称">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="账号">
                        <el-input v-model="form.detail"></el-input>
                    </el-form-item>
                    <el-form-item label="密码">
                        <el-input v-model="form.detail"></el-input>
                    </el-form-item>
                    <el-form-item label="角色描述">
                        <el-input v-model="form.detail"></el-input>
                    </el-form-item>
                </el-form>
                <el-tree :data="treeData" :props="defaultProps" show-checkbox></el-tree>
            </div>
        </el-dialog>
        
        <el-table :data="tableData">
            <el-table-column props="id" label="ID" align="center"></el-table-column>
            <el-table-column props="name" label="名称" align="center"></el-table-column>
            <el-table-column props="detail" label="描述" align="center"></el-table-column>
            <el-table-column props="username" label="账号" align="center"></el-table-column>
            <el-table-column props="password" label="密码" align="center"></el-table-column>
            <el-table-column props="phone" label="手机号" align="center"></el-table-column>
            <el-table-column label="操作" align="center" width="300px">
                <template slot-scope="scope">
                    <el-button type="success" size="mini">编辑权限列表</el-button>
                    <el-button type="warning" size="mini">禁用</el-button>
                    <el-button type="danger" size="mini">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import sidebar from '@/components/sidebar.js'
    export default {
        name: 'permission',
        data() {
            return {
                dialogRole: false,
                form: {
                    name: '',
                    detail: ''
                },
                treeData: sidebar,
                defaultProps: {
                    children: 'children',
                    label: 'title'
                },
                tableData: [{}]
            }
        },
        methods: {
            addRole() {
                var self = this
                self.dialogRole = true;
            }
        }
    }
</script>

